.feature {
  width: 100%;
  min-height: 855px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0;
  margin-top: 0;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
}

@media (max-width: 1366px) {
  .feature-phone {
    top: 126px;
    left: 50px;
  }
}

.skills-heading span {
  font-weight: 700;
}

.feature-style {
  padding: 0;
  margin-bottom: 0;
}

.feature-style li {
  position: relative;
  margin-bottom: 45px;
}
.feature-icon {
  position: absolute;
  padding: 0;
  top: 0;
}

.feature-subtitle h3 {
  padding-left: 35px;
  margin-top: 8px;
  line-height: 28px;
  font-size: 16px;
  font-weight: 600;
  margin-top: 30px;
  text-transform: capitalize;
  letter-spacing: 1px;
}
.feature-style p {
  margin-top: 10px;
  margin-bottom: 0;
  line-height: 1.8em;
  font-size: 15px;
  color: #777;
  letter-spacing: 0.03em;
  text-transform: capitalize;
}
